<template>
  <BaseLayout title="我的积分" bg="#1989fa" fg="#FFF" notitle>
    
    <div class="summary">
        <div class="desc1">账户积分</div>
        <number-roll :number="state.stat.balance" fg="#FFF" size="22px"><span style="color:#CCC;font-size:9px;">分</span></number-roll>
        <div class="desc2">累计收入：{{state.stat.reward}} 分，累计消费：{{state.stat.consume}} 分</div>
    </div>
    
    <div class="toolbar">
      <van-button class="btn" size="small" color="#39F" block @click="state.show = true">划拨</van-button>
      <van-button class="btn" size="small" color="#39F" block to="/my/exchange">转让</van-button>
    </div>

    <van-pull-refresh class="list" v-model="state.refreshing" @refresh="onChange">   
      <van-tabs v-model:active="state.active" color="#1989fa" offset-top="45" sticky swipeable animated @change="onChange">
        <van-tab v-for="(v,k) in state.category" :key="k" :title="v" >
          <van-list v-model:loading="state.loading" :finished="state.finished" loading-text="正在请求数据 ..." finished-text="没有更多了" :immediate-check="false" @load="onLoad">
            
            <van-empty v-show="state.list.length == 0"></van-empty>
            
            <van-cell-group class="block" :border="false" v-for="(item,index) in state.list" :key="index" @click="$toast(item.remark)">
              <van-cell class="item" :border="false" :title="item.remark">
                <template #extra><span :style="{color:item.color}">{{item.amount}} 分</span></template>
              </van-cell>
              <van-cell class="dt" :border="false" :title="item.dt" :value="'余额：' +  item.subtotal + ' 分'" ></van-cell>
            </van-cell-group>

          </van-list>
        </van-tab>
      </van-tabs>
    </van-pull-refresh>

    <van-popup v-model:show="state.show" round closeable position="bottom">
      <div class="transfer">
        积分划拨
        <van-field type="number" :border="1" v-model="state.mobile" input-align="right" clearable label="账号" placeholder="接收人UID或手机号"></van-field>
        <van-field type="number" :border="1" v-model="state.amount" input-align="right" clearable label="数量" placeholder="要划拨的积分数量"></van-field>
        <van-field v-model="state.remark" type="text" maxlength="20" input-align="right" label="备注" placeholder="可以不填" />
        <van-button round block type="primary" :style="{margin: '10px 0'}" @click="onTransfer">立即划拨</van-button>
      </div>
    </van-popup>

  </BaseLayout>
</template>

<style scoped>
.summary {
  margin: 0px 20px;
  padding: 5px 10px;
  background: #39F;
  border-radius: 5px;
  text-align: center;
}
.summary .desc1 {
  color: #FFF;
  font-size: 14px;
  font-weight: 600;
  text-align: left;
}
.summary .desc2 {
  color: #FFF;
  font-size: 12px;
  opacity: 0.6;
}

.toolbar {
  margin: 10px 17px;
  display: flex;
  justify-content: space-around;
  text-align: center;
}
.btn {
  margin: 3px;
}


.list {
  min-height: 500px;
  padding: 10px;
  background: #F9F9F9;
}
.block {
  margin-top: 20px;
  padding-bottom: 10px;
  border-radius: 10px;
  overflow: hidden;
}
.item {
  height: 40px;
  font-size: 16px;
  font-weight: 600;
  line-height: 26px;
}
.dt {
  height: 30px;
  padding-top: 2px;
  font-size: 12px;
}

.transfer {
  margin: 15px 10px;
  text-align: center;
}

</style>

<script>
import { reactive } from 'vue'
import BaseLayout from '@/components/BaseLayout.vue'
import NumberRoll from '@/components/NumberRoll.vue'

export default {
  components: {BaseLayout, NumberRoll},

  setup(){
    const state = reactive({
      stat: {
        balance: 0,
        freez: 0,
        transfer: 0,
        deposit: 0,
        withdrawal: 0,
        consume: 0,
        reward: 0,
      },

      active: 0, // 当前激活的标签
      refreshing: false,
      loading: false,
      finished: false,

      category: {
        all: "全部",
        reward: "收入",
        consume: "消费",
        //deposit: "充值",
        //withdrawal: "提现",
        user: "划转",
      },

      list: [],   // 列表
      offset: 0,  // 记录偏移量
      limit : 20, // 每页记录数量

      // 积分划拨
      mobile: "",
      amount: "",
      remark: "",
    });
    return {
      state,
    };
  },

  created() {
    this.onChange();
  },

  methods: {
    onChange() {
      this.state.show = false;
      this.state.loading = true;
      this.state.finished = false;
      this.state.offset = 0;
      this.state.list = [];
      this.onLoad();
      this.$http.get("/property/summary?subject=point").then((data) => this.state.stat = data);
    },
    onLoad() {
      this.$http.get("/property/detail?subject=point&type=" + Object.keys(this.state.category)[this.state.active] + "&offset=" + this.state.offset + "&limit=" + this.state.limit).then((data) => {
        this.state.refreshing = false;
        if (data.length == 0 || data.length < this.state.limit)
          this.state.finished = true;
        this.state.loading = false;
        this.state.offset += data.length;

        for (let i in data){
          let item = data[i];
          this.state.list.push({...item, ...{
            amount: item.amount.replace(/(\.\d\d)(\d+)$/, "$1"),
            subtotal: item.subtotal.replace(/(\.\d\d)(\d+)$/, "$1"),
            color : (item.amount > 0 ? "#090" : "#F66"),
          }});
        }
      });
    },
    onTransfer() {
      this.state.amount > 0 && this.$http.post("/property/transfer?u=" + this.state.mobile + "&amount=" + this.state.amount + "&subject=point&remark=" + this.state.remark).then(this.onChange);
    },
  }
}
</script>
